.dialogs{
	margin: 20px 0 40px 0px;
	width: 80%;
	display: flex;
	justify-content: center;
	height: 600px;
	z-index: 1;
}
.dialogs__dialogItems{
	flex: 0 0 25%;
}
.dialogs__messages{
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	flex: 0 0 80%;
	padding: 25px 50px;
}
